<template>
  <div class="text-center">
    <button 
      type="submit"
      class="btn btn-info btn-fill btn-wd" 
      @click.prevent="submitFunction"
    >
      <slot />
    </button>
    <router-link 
      :to="backUrl" 
      class="btn btn-danger btn-fill btn-wd"
    >
      Back
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'ButtonBlock',
  props: {
    backUrl: {
      default: '',
      type: String
    }
  },
  methods: {
    submitFunction() {
      this.$emit('submit')
    }
  }
}
</script>



